import React, {createContext, useContext, useEffect, useState} from 'react';

const ScrollControllerContext = createContext(null);

function ScrollController(props) {
  const [controller, setController] = useState(null);

  useEffect(() => {
    const scrollController = new window.ScrollMagic.Controller({
      // addIndicators: true
    });

    scrollController.scrollTo(position => {
      window.scroll({
        top: position,
        behavior: "smooth"
      });
    });

    setController(scrollController);
  }, []);

  return controller && (
    <ScrollControllerContext.Provider value={controller}>
      {props.children}
    </ScrollControllerContext.Provider>
  );
}

function useController() {
  return useContext(ScrollControllerContext);
}

export {useController};
export default ScrollController;