import {css, cx} from "emotion";
import React, {useEffect} from 'react';
import {useController} from "../ScrollController";

const container = css`
  padding: 25vh 0;
  background: #111;
  position: relative;
`;

const header = css`
  font-size: 90px;
  font-weight: 700;
  
  @media(max-width: 1024px) {
    font-size: 70px;
    font-weight: 700;
  }
`;

const subheader = css`
  font-size: 30px;
  font-weight: 700;
`;

const headerContainer = css`
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  
  text-transform: lowercase;
  color: white;
  min-width: 620px;

  @media(max-width: 1024px) {
    min-width: 500px;
  }
`;

const websiteImageContainer = css`
  width: 50%;
  display: flex;
  justify-content: center;
`;

const websiteSlide = css`
  padding: 5%;
  display: flex;
  height: 50vh;
  align-items: center;
  justify-content: center;
  background: #111;
`;

const slide = css`
   opacity: 0;

  .image-container {
    opacity: 0;
  }

  &.active {
    opacity: 1;
    z-index: 1;

    .image-container {
      transition: opacity 0.5s ease-out;
      opacity: 1;
      
      img {
        cursor: pointer;
      }
    }
  }
`;

const sliderImage = css`
  max-height: 80vh;
  max-width: 100%;
  width: auto;
  height: auto;
`;

function useSliderEffect(refs) {
    const controller = useController();

    useEffect(() => {
        const slides = refs.current;

        slides.forEach((slide, index) => {
            const isFirst = index === 0;
            const isLast = index === slides.length - 1;

            new window.ScrollMagic.Scene({
                triggerElement: slide,
                triggerHook: 0.25,
                duration: isLast ? "100%" : "50%"
            })
                .setPin(slide, {pushFollowers: false})
                .on('enter', () => {
                    slide.classList.add('active');
                })
                .on('leave', event => {
                    if (isFirst && event.scrollDirection === 'REVERSE') {
                        return;
                    }
                    slide.classList.remove('active');
                })
                .addTo(controller);
        });

    }, [refs]);
}

function Slider(props) {
    const refs = React.useRef([]);
    useSliderEffect(refs);

    return (
        <div className={container}>
            {props.urls.map((image, i) => (
                <div
                    key={i}
                    ref={ref => refs.current.push(ref)}
                    className={cx(websiteSlide, slide, 'slide', {active: i === 0})}
                >
                    <div className={headerContainer}>
            <span className={header}>
              {props.header}
            </span>
                        <span className={subheader}>
              {i + 1} of {props.urls.length}
            </span>
                    </div>

                    <div className={cx(websiteImageContainer, 'image-container')}>
                        {image}
                    </div>
                </div>
            ))}
        </div>
    );
}

export default Slider;