import React, {useEffect, useRef, useState} from 'react';
import {useNavigation} from "./navigation/Navigation";
import {useController} from "./ScrollController";

function NavigationPage(props) {
  const pageRef = useRef(null);
  const navigation = useNavigation();
  const controller = useController();

  const [scene, setScene] = useState(null);

  function onProgress(event) {
    if (event.state === 'DURING') {
      navigation.setActivePage(props.index);
    } else if (event.state === 'BEFORE') {
      navigation.setActivePage(props.index - 1);
    }
  }

  useEffect(() => {
    const element = pageRef.current;

    const scene = new window.ScrollMagic.Scene({
      triggerElement: element,
      triggerHook: 0
    })
      .addTo(controller);

    setScene(scene);

    scene.on('progress', onProgress);
    return () => scene.off('progress', onProgress);
  }, [controller]);

  useEffect(() => {
    if (scene) {
      navigation.register({
        name: props.name,
        index: props.index,
        scene
      });
    }
  }, [scene, navigation]);

  return (
    <div ref={pageRef}>
      {props.children}
    </div>
  );
}

export default NavigationPage;