import React from 'react';
import {Slider} from "../components";
import {css} from "emotion";

const sliderImage = css`
  max-height: 80vh;
  max-width: 100%;
  width: auto;
  height: auto;
`;


function Graphics() {
    const urls = [
        'illustrations/illustration1.jpg',
        "illustrations/illustration2.jpg",
        "illustrations/illustration4.jpg",
        "illustrations/illustration5.jpg",
        "illustrations/illustration6.jpg",
        "illustrations/illustration7.jpg",
        "illustrations/illustration8.jpg",
        "illustrations/illustration3.jpg"
    ];

    return (
        <Slider
            urls={urls.map(url => (
                <img
                    onClick={() => console.log(url)}
                    className={sliderImage}
                    alt="illustration"
                    src={url}
                />
            ))}
            header="my illustrations"
        />
    );
}

export default Graphics;