import React from 'react';
import {cx, css, keyframes} from 'emotion';
import WritingText from "./WritingText";

const dotsContainer = css`
  height: 100%;
  margin-right: 15px;
  display: flex;
  align-items: center;
  justify-content: flex-end;
`;

const dotsList = css`
  padding: 0;
  margin: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
`;

const dot = css`
  width: 10px;
  height: 10px;
  border-radius: 100%;
  margin: 10px;
  background: #fd981b;
  cursor: pointer;
`;

const active = css`
  background: transparent;
  box-shadow: 0 0 0 2px #fd981b;
`;

const pageName = css`
  color: #fd981b;
  transform: rotate(-90deg);
  transform-origin: top left;
  position: absolute;
  
  line-height: 30px;
`;

function Dots(props) {
  return (
    <div className={dotsContainer}>
      <div style={{position: 'relative'}}>
        <div className={pageName}>
          <WritingText key={props.value} width="100px">
            {props.name}
          </WritingText>
        </div>

        <ol className={dotsList}>
          {[...Array(props.count)].map((_, i) => (
            <li
              key={i}
              className={cx(dot, {[active]: i === props.value})}
              onClick={() => props.onChange(i)}
            />
          ))}
        </ol>
      </div>
    </div>
  );
}

export default Dots;