import React, {createContext, useCallback, useContext, useEffect, useMemo, useState} from "react";
import {Dots} from "../components";
import {useController} from "../ScrollController";

const NavigationContext = createContext({});

function Navigation(props) {
  const [elements, setElements] = useState([]);
  const [activePage, setActivePage] = useState(0);
  const controller = useController();

  const register = useCallback(({index, ...page}) => {
    if (elements[index]) {
      return;
    }
    setElements(elements => [...elements.slice(0, index), page, ...elements.slice(index + 1, elements.length - 1)]);
  }, [elements]);

  const scrollTo = useCallback(index => {
    controller.scrollTo(elements[index].scene);
  }, [controller, elements]);

  const navigationContext = useMemo(() => ({
    register,
    scrollTo,
    setActivePage
  }), [register, scrollTo, setActivePage]);

  return (
    <NavigationContext.Provider value={navigationContext}>
      {props.children}
      {elements[activePage] && (
        <div style={{height: '100vh', width: '50px', position: 'fixed', top: 0, right: 0}}>
          <Dots
            count={elements.length}
            value={activePage}
            name={elements[activePage].name}
            onChange={scrollTo}
          />
        </div>
      )}
    </NavigationContext.Provider>
  );
}

function useNavigation() {
  return useContext(NavigationContext);
}

export {useNavigation, NavigationContext}
export default Navigation;