import {css, cx, keyframes} from "emotion";
import React, {useCallback} from 'react';
import {OnePage, WritingTextSlider} from "../components";
import {useNavigation} from "../navigation/Navigation";
import {ReactSVG} from 'react-svg'

const center = css`
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const aboutMeContainer = css`
  height: 320px;
  width: 95%;
  display: flex;
  justify-content: center;
`;

const profileImageContainer = css`
  width: 320px;
  text-align: end;
`;

const profileImage = css`
  width: 300px;
  height: 300px;
  border-radius: 100%;
  border: 10px solid #fd981b;
`;

const textContainer = css`
  margin-left: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  max-width: 600px;
`;

const h3 = css`
  font-weight: 400;
  font-size: 19px;
`;

const h1 = css`
  font-size: 42px;
  font-weight: 700;
  text-transform: uppercase;
`;

const name = css`
  color: #fd981b;
  font-weight: 700;
`;

const p = css`
  line-height: 25px;
`;

const orange = css`
  color: #fd981b;
`;

const actions = css`
`;

const marginLeft15 = css`
  margin-left: 15px;
`;

const navigationButton = css`
  display: inline-block;
  width: 200px;
  line-height: 38px;
  padding: 0 20px;
  border-radius: 26px;
  text-align: center;
  text-decoration: none;
  font-size: 18px;
  font-weight: 500;
  text-transform: uppercase;
`;

const navigationPrimaryButton = css`
  ${navigationButton};
  
  background-color: #fd981b;
  color: white;
  border: 2px solid #fd981b;
`;

const navigationSecondaryButton = css`
  ${navigationButton};

  background-color: white;
  color: #fd981b;
  border: 2px solid #fd981b;
`;

const scrollKeyframes = keyframes`
  0%, 20%, 40% {
    transform: translateY(0);
  }
  10%, 30% {
    transform: translateY(-10px);
  }
`;

const scrollContainer = css`
  position: absolute;
  bottom: 10px;
  width: 100vw;
  text-align: center;
  
  animation: ${scrollKeyframes} 2s ease-out infinite;
`;

const smallText = css`
  font-size: 12px;
`;

function AboutMe() {
  const navigation = useNavigation();

  const handleWebsitesClick = useCallback(() => {
    navigation.scrollTo(1)
  }, [navigation]);

  const handleGraphicsClick = useCallback(() => {
    navigation.scrollTo(2)
  }, [navigation]);

  return (
    <OnePage>
      <div className={center}>
        <div className={aboutMeContainer}>
          <div className={profileImageContainer}>
            <img className={profileImage} src="./profile-image.jpg" alt="profile" />
          </div>
          <div className={textContainer}>
            <h3 className={h3}>HI THERE!</h3>
            <h1 className={h1}>
              I'm&nbsp;
              <WritingTextSlider
                width="460px"
                texts={[
                  <span className={orange}>Lusine&nbsp;Voskanyan</span>,
                  <span className={orange}>a&nbsp;web&nbsp;designer</span>,
                  <span className={orange}>a&nbsp;teacher</span>,
                ]}
              />
            </h1>
            <p className={p}>
              I addicted to taking photos, creating new things which can change my day for the better. So having various ideas and intentions,
              I have decided to keep in touch with graphic/web designing.
            </p>
            <div className={actions}>
              <a onClick={handleWebsitesClick} href="#" className={navigationPrimaryButton}>websites</a>
              <a onClick={handleGraphicsClick} href="#" className={cx(navigationSecondaryButton, marginLeft15)}>graphics</a>
            </div>
          </div>
        </div>
      </div>

      <div className={scrollContainer}>
        <ReactSVG
          src="./mouse-scroll.svg"
          color="orange"
          beforeInjection={svg => {
            svg.setAttribute('style', 'height: 60px; width: 60px')
          }}
        />
        <span className={smallText}>Scroll to explore</span>
      </div>
    </OnePage>
  );
}

export default AboutMe;