import {css, keyframes} from "emotion";
import React from 'react';

const widthKeyframes = width => keyframes`
  from {
    width: 0;
  }
  to {
    width: ${width};
  }
`;

const writingAnimation = width => css`
  display: inline-flex;
  overflow: hidden;
  animation: ${widthKeyframes(width)} 1s ease-out;
`;

function WritingText(props) {
  return (
    <span className={writingAnimation(props.width)}>
      {props.children}
    </span>
  );
}

export default WritingText;