import React from 'react';
import {Slider} from "../components";
import {css} from "emotion";

const sliderImage = css`
  max-height: 80vh;
  max-width: 100%;
  width: auto;
  height: auto;
`;

function Websites() {
    const websites = [
        {
            img: "websites/website1.jpeg",
            href: "Amaze/home"
        },
        //
        // "websites/website2.jpeg",
        // "websites/website3.jpeg",
        // "websites/website4.jpeg",
        // "websites/website5.jpeg",
        // "websites/website6.jpeg",
        // "websites/website7.jpeg",
        // "websites/website8.jpeg"
    ];

    return (
        <Slider
            urls={websites.map(({img, href}) => (
                <a target="_blank" href={href} rel="noopener noreferrer">
                    <img
                        className={sliderImage}
                        alt="website"
                        src={img}
                    />
                </a>
            ))}
            header="my websites"
            name='Websites'
        />
    );
}


export default Websites;