import React from 'react';
import Navigation from "./navigation/Navigation";
import NavigationPage from "./NavigationPage";
import {AboutMe, ContactMe, Graphics, Websites} from "./pages";
import ScrollController from "./ScrollController";


function App() {
  const pages = [{
    name: <span>about&nbsp;me</span>,
    content: <AboutMe />
  }, {
    name: <span>websites</span>,
    content: <Websites />
  }, {
    name: <span>graphics</span>,
    content: <Graphics />
  }, {
    name: <span>contact&nbsp;me</span>,
    content: <ContactMe />
  }];

  return (
    <ScrollController>
      <Navigation>

        {pages.map((page, index) => (
          <NavigationPage
            name={page.name}
            index={index}
            key={index}
          >
            {page.content}
          </NavigationPage>
        ))}

      </Navigation>
    </ScrollController>
  );
}

export default App;
